/* src/components/StudentList.css */
.student-list {
    margin-top: 40px;
    overflow-x: auto;
  }
  
  .student-list table {
    width: 100%;
    border-collapse: collapse;
    background-color: #ffffcc;
    border-radius: 8px;
    overflow: hidden;
  }
  
  .student-list th, .student-list td {
    padding: 12px;
    border: 1px solid #ddd;
    text-align: left;
  }
  
  .student-list th {
    background-color: #3bb54a;
    color: white;
  }
  